module.exports = {
	// ref (   )
	getRefs: function(ref, _this, count, fun) {
		if (count >= 50) {
			fun(_this.$refs[ref]);
			return false;
		}
		var refReturn = _this.$refs[ref];
		if (refReturn) {
			fun(refReturn);
		} else {
			count++;
			setTimeout(() => {
				this.getRefs(ref, _this, count, fun);
			}, 100);
		}
	},
	
}
